<script>
export default {
  name: "HelloWorld",
  data: function() {
    return {
      currentDem: null,
		demfiles: null,
		windowHeight: document.documentElement.clientHeight,
    };
  },
  mounted() {
    this.Initial();
  },
  methods: {
    Initial() {
      var _this = this;
      _this.axios.get("http://localhost:8000/api/dem").then(
        res => {
          _this.currentDem = res.data.current;
          _this.demfiles = res.data.allFiles;
        },
        res => {
          // 错误回调
          alert("没有找到后台服务");
        }
      );
    },
    ClickActive(index) {
      var _this = this;
      _this.axios.get("http://localhost:8000/api/dem/setdem/"+encodeURIComponent(_this.demfiles[index])).then(
        res => {
          _this.Initial();
        },
        res => {
          // 错误回调
          alert("没有找到后台服务");
        }
      );
    }
  }
};
</script>

