<template>
  <div>
    <HeaderTittle v-bind:Title="MethodName"></HeaderTittle>
    <div style="margin:10px">
      <div class="left-content">
        测试区域起始经度(度)：
        <input v-model="minlon" />
        起始纬度(度)：
        <input v-model="minlat" />
        截止经度(度)：
        <input v-model="maxlon" />
        截止纬度(度)：
        <input v-model="maxlat" />
        视点高度(米)：
        <input v-model="seeHeight" />
        随机测试次数：
        <input v-model="testCount" value="5" />
        <button @click="startTest" style="margin-left:10px">开始测试</button>
      </div>
      <div class="left-content">
        默认测试区域DEM格网为30米，数据为东经119°~120°，北纬41°~42°，避免边界运算出现错误，填写测试范围不要超过此范围，且应当适当缩小。
        同时，为避免计算区域过小，范围直径不可小于0.027(30米格网大概10个网格间距)。
      </div>
      <br />
      <HotTable
        :root="root"
        ref="testHot"
        :settings="hotSettings"
        style="overflow:auto; min-height: 400px;"
      ></HotTable>
      <div class="left-content">{{info}}</div>
    </div>
    <div v-bind:class="{coverLayout:isOnTesting}">
      <div style="font-size:50px;margin-top:400px;">正在计算...</div>
    </div>
  </div>
</template>

<script>
import { HotTable } from "@handsontable/vue";
import "../../node_modules/handsontable/dist/handsontable.full.css";
import Handsontable from "handsontable";
import { truncate } from "fs";
import HeaderTittle from "./HeaderTittle.vue";

export default {
  props: {
    analysisMethod: String,
    MethodName: String,
    HeaderTittle
  },
  data: function() {
    return {
      info: "",
      minlon: 119.15,
      maxlon: 119.95,
      minlat: 41.15,
      maxlat: 41.95,
      seeHeight: 2,
      testCount: 10,
      isOnTesting: false,
      root: "test-hot",
      hotSettings: {
        data: [],
        minRows: 1, //最小行列
        minCols: 5,
        maxRows: 10000, //最大行列
        maxCols: 20,
        rowHeaders: true, //行表头，可以使布尔值（行序号），可以使字符串（左侧行表头相同显示内容，可以解析html），也可以是数组（左侧行表头单独显示内容）。
        colHeaders: [
          "中心点坐标",
          "计算点总数",
          "通视率",
          "R3耗时(秒)",
          "OSD算法耗时(秒)",
          "OSD单点耗时(纳秒)",
          "误点数",
          "总错误率",
          "参考线交叉点数",
          "交叉点计算量",
          "交叉点求解失败量"
        ], //自定义列表头or 布尔值
        columns: [
          {
            data: "location",
            type: "text"
          },
          {
            data: "allPointCount",
            type: "numeric"
          },
          {
            data: "visibleRate",
            type: "numeric",
            renderer: this.percentageCellRender
          },
          {
            data: "r3Time",
            type: "numeric",
            numericFormat: {
              pattern: "0.00000"
            }
          },
          {
            data: "time",
            type: "numeric",
            numericFormat: {
              pattern: "0.00000"
            }
          },
          {
            data: "perPointTime",
            type: "numeric",
            numericFormat: {
              pattern: "0.00"
            }
          },
          {
            data: "osdAllErrCount",
            type: "numeric"
          },
          {
            data: "osdErrRate",
            type: "numeric",
            renderer: this.percentageCellRender
          },
          {
            data: "crossCount",
            type: "numeric"
          },
          {
            data: "crossCaculateCount",
            type: "numeric"
          },
          {
            data: "crossFailCount",
            type: "numeric"
          }
        ],
        currentRowClassName: "currentRow", //为选中行添加类名，可以更改样式
        currentColClassName: "currentCol", //为选中列添加类名
        autoWrapRow: true, //自动换行
        fillHandle: true, //选中拖拽复制 possible values: true, false, "horizontal", "vertical"
        fixedColumnsLeft: 0, //固定左边列数
        fixedRowsTop: 0, //固定上边列数
        manualColumnFreeze: true, //手动固定列
        manualColumnMove: true, //手动移动列
        manualRowMove: true, //手动移动行
        manualColumnResize: true, //手工更改列距
        manualRowResize: true, //手动更改行距
        comments: true, //添加注释
        columnSorting: true, //排序
        stretchH: "all", //根据宽度横向扩展，last:只扩展最后一列，none：默认不扩展
        licenseKey: "non-commercial-and-evaluation"
      }
    };
  },

  name: "handsonTable",
  components: {
    HotTable,
    HeaderTittle
  },
  methods: {
    percentageCellRender(instance, td, row, col, prop, value, cellProperties) {
      if (value != null) {
        if (value == 0) td.innerHTML = "0";
        else td.innerHTML = (value * 100).toFixed(4) + "%";
      }
      return td;
    },
    startTest() {
      var _this = this;
      _this.info = "";
      _this.isOnTesting = true;
      _this.axios
        .get(
          "http://localhost:8000/api/dem/" +
            _this.analysisMethod +
            "/" +
            _this.minlon +
            "/" +
            _this.maxlat +
            "/" +
            _this.maxlon +
            "/" +
            _this.minlat +
            "/" +
            _this.seeHeight +
            "/" +
            _this.testCount
        )
        .then(
          res => {
            _this.hotSettings.data = res.data;
            _this.isOnTesting = false;
            _this.info =
              "共" + res.data.length + "条数据，本表格最大能显示50条。";
          },
          res => {
            // 错误回调
            alert("出现错误");
            _this.isOnTesting = false;
          }
        );
    }
  }
};
</script> 

<style scoped>
input {
  width: 60px;
}
.left-content {
  text-align: left;
}
.coverLayout {
  position: absolute;
  left: 0;
  top: 0;
  background: rgb(214, 214, 214);
  width: 100%;
  height: 100%;
  z-index: 100;
  filter: alpha(Opacity=50);
  -moz-opacity: 0.5;
  opacity: 0.8;
}
</style>
