<template>
  <div>
    <table style="width: 100%">
      <tr>
        <td>
          <div style="background-color: #2fa8ec">
            Cesium场景
            <router-link to="/">返回首页</router-link>
          </div>
        </td>
        <td>
          <div style="background-color: yellow">调试操作</div>
        </td>
      </tr>
      <tr>
        <td valign="top">
          <div id="cesiumContainer" class="cesium-viewer"></div>
        </td>
        <td>
          <div style="width: 300px; text-align: left">
            <div style="overflow-y: scroll">
              <div>
                鼠标实时位置：
                <hr
                  style="
                    height: 1px;
                    border: none;
                    border-top: 2px ridge #185598;
                  "
                />
                <div>
                  经度：{{ mouseLocation.lon }} 纬度：{{ mouseLocation.lat }}
                </div>
                <div>高度：{{ mouseLocation.h }}</div>
                <div>
                  观测者视点离地高度(米)：
                  <input
                    v-model="seeHeight"
                    style="width: 40px"
                    placeholder="观测点高"
                  />
                </div>

                <br />实时测试:
                <br />
                <hr
                  style="
                    height: 1px;
                    border: none;
                    border-top: 2px ridge #185598;
                  "
                />
                <input
                  type="checkbox"
                  v-model="realTimeTest"
                  true-value="true"
                  false-value="false"
                />
                执行实时测试<br />
                测试半径(度):
                <input
                  v-model="realTimeTestR"
                  placeholder="单位为度"
                  style="width: 40px"
                />({{ realTimeTestRStr }})
                <div>
                  发送计数：{{ realTestSendCount }} 接收计数：{{
                    realTestReciveCount
                  }}
                </div>
                <div>{{ realTestTimeStr }}</div>

                <br />与R3精度对比（R3较慢，需等待）:
                <br />
                <hr
                  style="
                    height: 1px;
                    border: none;
                    border-top: 2px ridge #185598;
                  "
                />
                起点:
                <input
                  v-model="testLong"
                  placeholder="测试经度"
                  style="width: 100px"
                />
                <input
                  v-model="testLat"
                  placeholder="测试纬度"
                  style="width: 100px"
                />
                <br />终点:
                <input
                  v-model="testToLong"
                  placeholder="测试经度"
                  style="width: 100px"
                />
                <input
                  v-model="testToLat"
                  placeholder="测试纬度"
                  style="width: 100px"
                />
                <br />
                <button @click="testOSD">PDERL</button>
                <button @click="testFOSD">XPDERL</button>
                <button @click="testXDraw">XDraw</button>
                <button @click="testRefF">参考面</button>
                <br />
                <button @click="computHeight">获取高度</button>
                <button @click="lookAtTestPoint">定位视角</button>
                <button @click="lookLeft10">←</button>
                <button @click="lookRight10">→</button>
                <button @click="lookUp5">↑</button>
                <button @click="lookDown5">↓</button>
                <br />
                <br />通视分析信息：
                <hr
                  style="
                    height: 1px;
                    border: none;
                    border-top: 2px ridge #185598;
                  "
                />
                <div>
                  半径: {{ lastDist.toFixed(4) }}米；高程:
                  {{ startPointServerHeightStr }}
                </div>
                <div>
                  计算时间（不包括显示时间）：
                  <br />
                  {{ TimeSpan }}
                </div>
                <div>
                  计算点量：
                  {{ CaculateCount }}
                </div>

                <br />输出信息：
                <hr
                  style="
                    height: 1px;
                    border: none;
                    border-top: 2px ridge #185598;
                  "
                />
                <div>{{ info }}</div>
              </div>
            </div>
          </div>
        </td>
      </tr>
    </table>
  </div>
</template>

<script>
//import store from './store'
import Cesium from "cesium/Cesium";
// noinspection ES6UnusedImports
import widget from "cesium/Widgets/widgets.css";

export default {
  name: "CesiumViewer",
  data() {
    return {
      lastlon: 0,
      lastlat: 0,
      /**
       * cesium视图
       * @type{Cesium.Viewer}
       */
      viewer: null,
      /**
       * 地形高度适配器
       */
      terrainProvider: null,
      /**
       * 场景
       */
      scene: null,
      /**
       * 变换
       */
      ellipsoid: null,
      /**
       * 观测点高（如果贴近地面很可能会被周围地形大量遮挡）
       */
      seeHeight: 5,
      /**
       * 测试经度
       */
      testLong: 119.5,
      /**
       * 测试纬度
       */
      testLat: 41.5,
      /**
       * 测试半径(度)
       */
      testToLong: 119.55,
      testToLat: 41.55,
      /**
       * 实时测试的半径
       */
      realTimeTestR: 0.05,
      /**
       * 是否开启实时测试
       */
      realTimeTest: false,
      /**
       * 是否正在实时绘制测试
       */
      realTimeTestOnDrawing: false,
      /**
       * 实时测试发送数据计数
       */
      realTestTime: null,
      /**
       * 实时测试发送数据计数
       */
      realTestTimeStr: "",
      /**
       * 实时测试发送数据计数
       */
      realTestSendCount: 0,
      /**
       * 实时测试接收数据计数
       */
      realTestReciveCount: 0,
      /**
       * 实时测试绘制更新队列
       */
      realTimeTestDrawArr: new Array(),
      /**
       * 实时测试绘制更新队列
       */
      realTimeTestTimer: null,
      /**
       * 计算点数
       */
      CaculateCount: 0,
      /**
       * 通过比较进行计算的点数
       */
      CompareCount: 0,
      /**
       * 花费时间
       */
      TimeSpan: null,
      /**
       * 是否正在绘制的状态
       * @type{Boolean}
       */
      ondrawing: false,
      /**
       * 鼠标点击的点
       */
      mouseLocation: { lon: "...", lat: "...", h: "..." },
      /**
       * 用于输出调试信息
       */
      info: "infomation....",
      /**
       * 量测的站立点
       */
      startPoint: null,
      /**
       * 测量站立点（经纬度形式）
       * @type{Cartographic}
       */
      startCartographic: null,
      /**
       * 测量站立点（经纬度形式）
       * @type{Cartographic}
       */
      toCartographic: null,
      /**
       * 站立点位置描述字符串
       */
      startPointStr: "...",
      /**
       * 站立点服务器测算高
       */
      startPointServerHeight: 0,
      /**
       * 圈半径(动态更新的半径长度,Cesium.CallbackProperty(()=>number,false))来达到动态更新的效果
       * @type{number}
       */
      dist: 10,
      /**
       * 最近一次判定距离
       */
      lastDist: 0,
      /**
       * 绘制的⚪
       * @type{object}
       */
      circle: null,
      /**
       * 保存所有已经绘制的图形，便于在右键的时候统一删除
       */
      shapes: [],
      /**
       * 用于保存实时绘制
       */
      updateShape: null,
      /**
       * 用于保存实时绘制
       */
      context: null,
    };
  },
  mounted() {
    var _this = this;
    var socket = new WebSocket("ws://localhost:8000");
    socket.onopen = function (event) {};
    socket.onclose = function (event) {};
    socket.onmessage = function (event) {
      _this.realTestReciveCount++;

      //加入显示队列
      _this.realTimeTestDrawArr.push(event.data);
      _this.realTestTimeStr +=
        "|接收" + (new Date().valueOf() - _this.realTestTime.valueOf());
      _this.realTestTime = new Date();
      //_this.updateVisibleLines();
    };

    _this.realTimeTestTimer = window.setInterval(function () {
      if (_this.realTimeTestDrawArr.length > 0) {
        _this.updateVisibleLines(_this.realTimeTestDrawArr.pop());
        _this.realTimeTestDrawArr.length = 0;
      }
    }, 200);

    //this.axios.get("/api/dem/setdem/ASTGTM2_N41E119_dem_平滑1次_拔高2倍");
    Cesium.Ion.defaultAccessToken =
      // "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJqdGkiOiJlM2RhZjk4ZC05MjY5LTRkNTUtOTg5NS1kM2YxMjFlZDRhOTUiLCJpZCI6OTgxOSwic2NvcGVzIjpbImFzciIsImdjIl0sImlhdCI6MTU1NDk5NDE0OH0.7-3_3OGYrYSmxUL6v6YvNaKh74hh-KcFGz-DVFrwO-g";
      "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJqdGkiOiJhZjg4ZDBjZi1lN2JiLTQwNzQtYjRiNS03ZDJiZTYzOTBkYTciLCJpZCI6OTgxOSwic2NvcGVzIjpbImFzbCIsImFzciIsImdjIl0sImlhdCI6MTU4NDk3MzIwNX0.busTyop6rf2-AWuiBtNnFOQnfXXHc6Dm-BVVg4Hs1hk";

    var onDrawing = this.ondrawing;
    /**
     * 当前要绘制的半径长度，用于动态更新
     * @type{Cesium.CallbackProperty}
     */
    var dynamicRadius = new Cesium.CallbackProperty(function () {
      return _this.dist;
    }, false);

    var demfile = "";

    ///terrain_tiles
    var terrainProvider = (_this.terrainProvider = Cesium.createWorldTerrain());
    //  new Cesium.CesiumTerrainProvider(
    //   {
    //     url: "/ASTGTM2_N22E114_dem"
    //     // url: "/ASTGTM2_N34E114_dem"
    //   })
    //   );
    var viewer = (_this.viewer = new Cesium.Viewer("cesiumContainer", {
      selectionIndicator: false,
      infoBox: false,
      terrainProvider: terrainProvider, //Cesium.createWorldTerrain()//terrainProvider
    }));
    viewer._cesiumWidget._creditContainer.style.display = "none";
    //禁止双击？
    viewer.cesiumWidget.screenSpaceEventHandler.removeInputAction(
      Cesium.ScreenSpaceEventType.LEFT_DOUBLE_CLICK
    );

    var scene = (_this.scene = viewer.scene);
    var ellipsoid = (_this.ellipsoid = viewer.scene.globe.ellipsoid);

    // 设置相机位置
    viewer.camera.lookAt(
      // Cesium.Cartesian3.fromDegrees(114.8439, 34.8113, 1000.0),
      Cesium.Cartesian3.fromDegrees(119.8439, 41.8113, 1000.0),
      new Cesium.Cartesian3(0.0, -1000.0, 1000.0)
    );
    viewer.camera.lookAtTransform(Cesium.Matrix4.IDENTITY);

    var handler = new Cesium.ScreenSpaceEventHandler(viewer.canvas);
    //单击事件
    handler.setInputAction(function (event) {
      if (!Cesium.Entity.supportsPolylinesOnTerrain(viewer.scene)) {
        console.log("This browser does not support polylines on terrain.");
        return;
      }

      // We use `viewer.scene.pickPosition` here instead of `viewer.camera.pickEllipsoid` so that
      // we get the correct point when mousing over terrain.
      var earthPosition = viewer.scene.pickPosition(event.position);
      var carto = ellipsoid.cartesianToCartographic(earthPosition);

      //查看是否在绘制范围的阶段
      if (onDrawing) {
        //已经在绘制阶段了，说明这一点将为绘制的终点
        onDrawing = false;

        _this.testToLong = Cesium.Math.toDegrees(carto.longitude).toFixed(6);
        _this.testToLat = Cesium.Math.toDegrees(carto.latitude).toFixed(6);
        terminateShape();
      } else {
        onDrawing = true;

        //alert("tt");
        _this.startPoint = earthPosition;
        _this.startCartographic = carto;
        //还未在绘制阶段将进入绘制阶段
        //绘制起点
        var p = viewer.entities.add({
          position: _this.startPoint,
          point: {
            color: Cesium.Color.RED,
            outlineColor: Cesium.Color.WHITE,
            outlineWidth: 2,
            pixelSize: 5,
            heightReference: Cesium.HeightReference.CLAMP_TO_GROUND,
          },
        });
        _this.shapes.push(p);

        _this.circle = viewer.entities.add({
          position: _this.startPoint,
          name: "Green circle at height",
          ellipse: {
            outlineColor: Cesium.Color.WHITE,
            outlineWidth: 2,
            semiMinorAxis: dynamicRadius,
            semiMajorAxis: dynamicRadius,
            material: Cesium.Color.BLUE.withAlpha(0.3),
          },
        });
      }
    }, Cesium.ScreenSpaceEventType.LEFT_CLICK);

    //鼠标移动事件
    handler.setInputAction(function (movement) {
      if (onDrawing) {
        var newPosition = viewer.scene.pickPosition(movement.endPosition);
        if (Cesium.defined(newPosition)) {
          var dist = Math.sqrt(
            Math.pow(newPosition.x - _this.startPoint.x, 2) +
              Math.pow(newPosition.y - _this.startPoint.y, 2) +
              Math.pow(newPosition.z - _this.startPoint.z, 2)
          );
          _this.dist = dist;
        }
      }
      var cartesian = viewer.scene.pickPosition(movement.endPosition);
      if (cartesian) {
        var cartographic = Cesium.Cartographic.fromCartesian(cartesian);

        var lon = Cesium.Math.toDegrees(cartographic.longitude);
        var lat = Cesium.Math.toDegrees(cartographic.latitude);
        var longitudeString = lon.toFixed(4);
        var latitudeString = lat.toFixed(4);

        var eartlonPosition = scene.globe.ellipsoid.cartesianToCartographic(
          cartesian
        );

        var ray = viewer.camera.getPickRay(movement.endPosition);
        var cartesian2 = viewer.scene.globe.pick(ray, viewer.scene);
        var cartographic2 = Cesium.Cartographic.fromCartesian(cartesian2);

        _this.mouseLocation.lon = longitudeString + "°";
        _this.mouseLocation.lat = latitudeString + "°";
        _this.mouseLocation.h = cartographic2.height.toFixed(4) + "米（概略）";

        if (_this.realTimeTest == "true") {
          if (
            Math.abs(_this.lastlon - lon) > 0.0001 ||
            Math.abs(_this.lastlat - lat) > 0.0001
          ) {
            _this.realTestTime = new Date();
            _this.realTestTimeStr = "发送";
            var lon2 = lon + _this.realTimeTestR;
            socket.send(
              lon + "," + lat + "," + lon2 + "," + lat + "," + _this.seeHeight
            );

            _this.realTestTime = new Date();
            _this.realTestSendCount++;

            _this.lastlon = lon;
            _this.lastlat = lat;
          }
        } else {
          if (_this.updateShape != null) {
            _this.viewer.entities.remove(_this.updateShape);
            _this.updateShape = null;
          }
          //window.clearInterval(_this.realTimeTestTimer);
          _this.realTestTimeStr = "";
        }
      }
    }, Cesium.ScreenSpaceEventType.MOUSE_MOVE);

    // Redraw the shape so it's not dynamic and remove the dynamic shape.
    function terminateShape() {
      //viewer.entities.remove(activeShape);
      _this.testLong = Cesium.Math.toDegrees(
        _this.startCartographic.longitude
      ).toFixed(4);
      _this.testLat = Cesium.Math.toDegrees(
        _this.startCartographic.latitude
      ).toFixed(4);

      _this.startPointStr =
        "lon:" +
        Cesium.Math.toDegrees(_this.startCartographic.longitude).toFixed(4) +
        "°  lat:" +
        Cesium.Math.toDegrees(_this.startCartographic.latitude).toFixed(4) +
        "°";
      _this.lastDist = _this.dist;

      //获取指定位置的高度
      _this.axios
        .get(
          "/api/dem/height/" +
            _this.testLong +
            "/" +
            _this.testLat
        )
        .then(
          (res) => {
            _this.startPointServerHeight = res.data;
          },
          (res) => {
            // 错误回调
          }
        );

      viewer.entities.remove(_this.circle);
      _this.circle = null;

      _this.dist = 10;
      //避免区域间相互影响
      dynamicRadius = new Cesium.CallbackProperty(function () {
        return _this.dist;
      }, false);

      //进行通视分析
      _this.viewshedDefault(
        _this.testLong,
        _this.testLat,
        _this.testToLong,
        _this.testToLat,
        _this.seeHeight
      );
    }
    handler.setInputAction(function (event) {
      onDrawing = false;
      //alert('rr');

      $.each(_this.shapes, (i, item) => {
        viewer.entities.remove(item);
      });
      _this.shapes = [];
    }, Cesium.ScreenSpaceEventType.RIGHT_CLICK);
  },
  methods: {
    stringToByte(str) {
      var bytes = new Array();
      var len, c;
      len = str.length;
      for (var i = 0; i < len; i++) {
        c = str.charCodeAt(i);
        if (c >= 0x010000 && c <= 0x10ffff) {
          bytes.push(((c >> 18) & 0x07) | 0xf0);
          bytes.push(((c >> 12) & 0x3f) | 0x80);
          bytes.push(((c >> 6) & 0x3f) | 0x80);
          bytes.push((c & 0x3f) | 0x80);
        } else if (c >= 0x000800 && c <= 0x00ffff) {
          bytes.push(((c >> 12) & 0x0f) | 0xe0);
          bytes.push(((c >> 6) & 0x3f) | 0x80);
          bytes.push((c & 0x3f) | 0x80);
        } else if (c >= 0x000080 && c <= 0x0007ff) {
          bytes.push(((c >> 6) & 0x1f) | 0xc0);
          bytes.push((c & 0x3f) | 0x80);
        } else {
          bytes.push(c & 0xff);
        }
      }
      return bytes;
    },
    viewshedDefault(fromLon, fromLat, toLon, toLat, seeHeight) {
      //进行通视分析
      var _this = this;
      // _this.now = new Date().getMilliseconds();
      this.axios
        .get(
          "/api/dem/analysis/" +
            fromLon +
            "/" +
            fromLat +
            "/" +
            toLon +
            "/" +
            toLat +
            "/" +
            seeHeight
        )
        .then(
          (res) => {
            // var n1 = new Date().getMilliseconds();
            var geometryInstances = new Array();
            _this.CaculateCount = res.data.allPointCount;
            _this.TimeSpan = res.data.time;
            _this.drawVisibleLines(res.data.visiblePoints);

            // var n2 = new Date().getMilliseconds();
          },
          (res) => {
            // 错误回调
          }
        );
    },
    updateVisibleLines(data) {
      var _this = this;
      //分段
      var arr = data.split("|");
      var heif = arr[0].split(",");
      var hie = Cesium.Cartesian3.fromDegreesArray([
        parseFloat(heif[0]),
        parseFloat(heif[1]),
        parseFloat(heif[2]),
        parseFloat(heif[3]),
        parseFloat(heif[4]),
        parseFloat(heif[5]),
        parseFloat(heif[6]),
        parseFloat(heif[7]),
        parseFloat(heif[8]),
        parseFloat(heif[9]),
      ]);
      var xy = arr[1].split(",");
      var x = parseInt(xy[0]);
      var y = parseInt(xy[1]);
      var values = _this.stringToByte(arr[2]);

      var c = document.createElement("canvas");
      c.width = x;
      c.height = y;
      var ctx = c.getContext("2d");
      //ctx.globalAlpha = 0.2;
      ctx.fillStyle = "rgba(0,255,0,0.2)";
      // //绘制文本
      // ctx.fillText(text, 0, 0);
      for (var i = 0; i < x; i++)
        for (var j = 0; j < y; j++) {
          if (values[i * x + j] == 1) ctx.fillRect(i, y - 1 - j, 1, 1);
        }

      _this.context = c;

      if (_this.updateShape == null) {
        var shape = {
          name: "Red polygon on surface",
          polygon: {
            hierarchy: hie,
            material: new Cesium.ImageMaterialProperty({
              image: _this.material,
              transparent: true,
              color: Cesium.Color.WHITE,
              transparent: 0.5,
            }),
            // stRotation: Cesium.Math.toRadians(this.style.stRotation),
            classificationType: Cesium.ClassificationType.BOTH,
          },
        };
        _this.updateShape = _this.viewer.entities.add(shape);
      } else {
        _this.updateShape._polygon.hierarchy = hie;
        _this.updateShape._polygon.material.image = _this.context;
      }
    },
    /**
     * 绘制可见点连线
     */
    drawVisibleLines(visiblePoints, visibleErrPoints, unvisibleErrPoints) {
      var _this = this;

      var shape = _this.viewer.entities.add({
        name: "Red polygon on surface",
        polygon: {
          hierarchy: Cesium.Cartesian3.fromDegreesArray(
            visiblePoints.hierarchy
          ),
          material: new Cesium.ImageMaterialProperty({
            image: _this.drawPointFaces(
              visiblePoints,
              visibleErrPoints,
              unvisibleErrPoints
            ),
            transparent: true,
            color: Cesium.Color.WHITE,
            transparent: 0.5,
          }),
          // stRotation: Cesium.Math.toRadians(this.style.stRotation),
          classificationType: Cesium.ClassificationType.BOTH,
        },
      });
      _this.shapes.push(shape);

      shape = _this.viewer.entities.add({
        position: _this.startPoint,
        name: "Green circle at height",
        ellipse: {
          outlineColor: Cesium.Color.WHITE,
          outlineWidth: 5,
          semiMinorAxis: 5,
          semiMajorAxis: 5,
          material: Cesium.Color.RED,
        },
      });
      _this.shapes.push(shape);
    },
    drawPointFaces(visiblePoints, visibleErrPoints, unvisibleErrPoints) {
      var c = document.createElement("canvas");
      c.width = visiblePoints.x * 2;
      c.height = visiblePoints.y * 2;
      var ctx = c.getContext("2d");
      //ctx.globalAlpha = 0.2;
      ctx.fillStyle = "rgba(0,255,0,0.2)";
      // //绘制文本
      // ctx.fillText(text, 0, 0);
      for (var i = 0; i < visiblePoints.x; i++)
        for (var j = 0; j < visiblePoints.y; j++) {
          if (visiblePoints.values[i][j] == 1)
            ctx.fillRect(i * 2, (visiblePoints.y - 1 - j) * 2, 2, 2);
        }
      if (visibleErrPoints != undefined) {
        ctx.fillStyle = "rgba(0,0,255,0.8)";
        for (var i = 0; i < visibleErrPoints.length; i++) {
          ctx.fillRect(
            visibleErrPoints[i].i * 2,
            (visiblePoints.y - 1 - visibleErrPoints[i].j) * 2,
            2,
            2
          );
        }
      }
      if (unvisibleErrPoints != undefined) {
        ctx.fillStyle = "rgba(255,0,0,0.8)";
        for (var i = 0; i < unvisibleErrPoints.length; i++) {
          ctx.fillRect(
            unvisibleErrPoints[i].i * 2,
            (visiblePoints.y - 1 - unvisibleErrPoints[i].j) * 2,
            2,
            2
          );
        }
      }
      return c;
    },

    testOSD() {
      this.testVisibleAnalysis("osd");
    },
    testFOSD() {
      this.testVisibleAnalysis("fast_osd");
    },
    testXDraw() {
      this.testVisibleAnalysis("xdraw");
    },
    testRefF() {
      this.testVisibleAnalysis("reff");
    },
    testVisibleAnalysis(method) {
      //进行通视分析
      var _this = this;
      _this.axios
        .get(
          "/api/dem/analysis_" +
            method +
            "/" +
            _this.testLong +
            "/" +
            _this.testLat +
            "/" +
            _this.testToLong +
            "/" +
            _this.testToLat +
            "/" +
            _this.seeHeight
        )
        .then(
          (res) => {
            var geometryInstances = new Array();
            _this.CaculateCount = res.data.allPointCount;
            _this.TimeSpan = res.data.time;
            _this.drawVisibleLines(
              res.data.visiblePoints,
              res.data.visibleErrPoints,
              res.data.unVisibleErrPoints
            );
            _this.info =
              "计算点数：" +
              res.data.allPointCount +
              ";\r\n通视率：" +
              (res.data.visibleRate * 100).toFixed(4) +
              "%" +
              ";\r\n可见点错误数：" +
              res.data.visibleErrCount +
              ";\r\n可见点错误率：" +
              (res.data.visibleErrRate * 100).toFixed(4) +
              "%" +
              ";\r\n不可见点错误数：" +
              res.data.unVisibleErrCount +
              ";\r\n不可见点错误率：" +
              (res.data.unVisibleErrRate * 100).toFixed(4) +
              "%" +
              ";\r\n错误率：" +
              (res.data.errRate * 100).toFixed(4) +
              "%;";
          },
          (res) => {
            // 错误回调
          }
        );
    },
    lookAtTestPoint() {
      var _this = this;
      _this.axios
        .get(
          "/api/dem/height/" +
            _this.testLong +
            "/" +
            _this.testLat
        )
        .then(
          (res) => {
            _this.startPointServerHeight = res.data;
            _this.viewer.camera.setView({
              destination: Cesium.Cartesian3.fromDegrees(
                Number(_this.testLong),
                Number(_this.testLat),
                _this.startPointServerHeight + Number(_this.seeHeight)
              ),
              orientation: {
                heading: Cesium.Math.toRadians(0.0), //默认值
                pitch: Cesium.Math.toRadians(0.0), // 默认值
                roll: 0.0, //默认值
              },
            });
          },
          (res) => {
            // 错误回调
          }
        );

      //this.viewer.camera.lookLeft(90);
      // this.viewer.camera.look(
      //   Cesium.Cartesian3.fromDegrees(
      //     Number(this.testLong),
      //     Number(this.testLat),
      //     this.startPointServerHeight + Number(this.seeHeight)
      //   ),
      //   0
      // );
    },
    lookLeft10() {
      this.viewer.camera.lookLeft(Cesium.Math.toRadians(10));
    },
    lookRight10() {
      this.viewer.camera.lookRight(Cesium.Math.toRadians(10));
    },
    lookUp5() {
      this.viewer.camera.lookUp(Cesium.Math.toRadians(5));
    },
    lookDown5() {
      this.viewer.camera.lookDown(Cesium.Math.toRadians(5));
    },
    /**
     * 对地形通视性开始进行计算
     */
    computHeight() {
      var start = Date.now();
      var positions = [
        Cesium.Cartographic.fromDegrees(
          Number(this.testLong),
          Number(this.testLat)
        ),
      ];
      var _this = this;
      var promise = Cesium.sampleTerrain(_this.terrainProvider, 15, positions);
      Cesium.when(promise, function (updatedPositions) {
        _this.info = updatedPositions;
        console.log(Date.now() - start);
      });
    },
  },
  computed: {
    startPointServerHeightStr: function () {
      return this.startPointServerHeight == 0
        ? "未计算"
        : this.startPointServerHeight.toFixed(4) + "米";
    },
    realTimeTestRStr: function () {
      return "大约实地" + this.realTimeTestR * 108 + " km";
    },
  },
};
</script>

