.wrapper {
  position: relative;
  border: 1px solid;
}
.left {
  position: fixed;
  margin-right: 300px;
  bottom: 20px;
  width: 100%;
  top: 1px;
  background-color: #2fa8ec;
}
.right {
  float: right;
  top: 0;
  bottom: 0;
  width: 298px;
  right: 0;
  text-align: left;
}
</style>
